* {
  cursor: url('../images/pencil-cursor.png'), auto;
}

a,
h1,
h2,
h3,
h4,
p {
  margin: 0px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  &:hover {
    cursor: pointer;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  display: block;
}

button {
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  &:hover {
    cursor: pointer;
  }
}
